import {
    Component,
    Host,
    h,
    Prop,
    Event,
    EventEmitter,
    Listen,
    Method,
} from '@stencil/core';
import {
    PromptResponseAnimation,
    PromptResponseScore,
    PromptMessage,
} from '../../../types';

/**
 * @category Prompt
 * @description A message thread component for displaying user and chatbot messages in a conversation format. Supports auto-scrolling, empty states, loading indicators, and message interaction events.
 * @slot thread-empty-state - Content is placed within the empty state wrapper. Used for placing custom empty state content.
 * @slot thread-messages - Content is placed within the messages wrapper. Used for placing custom messages content.
 */
@Component({
    tag: 'at-prompt-thread',
    shadow: false,
})
export class AtPromptThread {
    /**
     * Array of messages to display in the conversation thread
     */
    @Prop({ mutable: true }) messages: PromptMessage[] = [];

    /**
     * Shows a loading indicator for incoming messages
     */
    @Prop() loading: boolean = false;

    /**
     * Automatically scroll to the bottom when new messages are added
     */
    @Prop() auto_scroll: boolean = true;

    /**
     * Display name for chatbot/assistant messages
     */
    @Prop() chatbot_title: string = 'Assistant';

    /**
     * Display voting actions for assistant messages
     */
    @Prop() enable_vote: boolean = true;

    /**
     * Display copy action for assistant messages
     */
    @Prop() enable_copy: boolean = true;

    /**
     * Display edit action for user messages
     */
    @Prop() enable_edit: boolean = false;

    /**
     * Enable streaming text animations for system/assistant messages
     * - 'none': No animation (default)
     * - 'fade': Fade in the entire message
     * - 'words': Animate words appearing sequentially like ChatGPT
     */
    @Prop() response_animation: PromptResponseAnimation = 'words';

    /**
     * Emitted when a message copy action is requested
     */
    @Event() atMessageCopy: EventEmitter<{
        messageId: string;
        content: string;
    }>;

    /**
     * Emitted when a message retry action is requested
     */
    @Event() atMessageRetry: EventEmitter<{ messageId: string }>;

    /**
     * Emitted when a message edit action is requested
     */
    @Event() atMessageEdit: EventEmitter<{
        messageId: string;
        content: string;
    }>;

    /**
     * Emitted when a message vote action is requested
     */
    @Event() atMessageVote: EventEmitter<{
        messageId: string;
        score: number;
    }>;

    /**
     * @slot messages - Custom message content (alternative to using the messages prop)
     */

    private scrollContainer: HTMLElement;

    componentDidUpdate() {
        if (this.auto_scroll && this.scrollContainer) {
            this.scrollToBottom();
        }
    }

    /**
     * Scrolls the thread to the bottom with smooth animation
     */
    @Method()
    async scrollToBottom(): Promise<void> {
        return new Promise<void>((resolve) => {
            requestAnimationFrame(() => {
                if (this.scrollContainer) {
                    this.scrollContainer.scrollTo({
                        top: this.scrollContainer.scrollHeight,
                        behavior: 'smooth',
                    });
                }
                resolve();
            });
        });
    }

    /**
     * Scrolls the last user message to the top of the viewport
     */
    @Method()
    async scrollLastMessageToTop(): Promise<void> {
        return new Promise<void>((resolve) => {
            requestAnimationFrame(() => {
                if (!this.scrollContainer) {
                    resolve();
                    return;
                }

                const userMessages =
                    this.scrollContainer.querySelectorAll('[data-role="user"]');
                if (!userMessages.length) {
                    resolve();
                    return;
                }

                const lastUserMessage = userMessages[
                    userMessages.length - 1
                ] as HTMLElement;
                lastUserMessage.scrollIntoView({
                    behavior: 'smooth',
                    block: 'start',
                });
                resolve();
            });
        });
    }

    @Listen('atVote')
    handleVote(event: CustomEvent<{ messageId: string; score: number }>) {
        event.stopPropagation();

        const messageIndex = this.messages.findIndex(
            (msg) => msg.id === event.detail.messageId,
        );
        if (messageIndex !== -1) {
            const updatedMessages = [...this.messages];
            updatedMessages[messageIndex] = {
                ...updatedMessages[messageIndex],
                score: event.detail.score,
            };
            this.messages = updatedMessages;

            this.atMessageVote.emit(event.detail);
        }
    }

    @Listen('atCopy')
    handleMessageCopy(event: CustomEvent<string>) {
        const messageIndex = this.getMessageIndexFromEvent(event);
        if (messageIndex !== -1) {
            this.atMessageCopy.emit({
                messageId: this.messages[messageIndex].id,
                content: event.detail,
            });
        }
    }

    @Listen('atRetry')
    handleMessageRetry(event: CustomEvent<void>) {
        const messageIndex = this.getMessageIndexFromEvent(event);
        if (messageIndex !== -1) {
            this.atMessageRetry.emit({
                messageId: this.messages[messageIndex].id,
            });
        }
    }

    @Listen('atEdit')
    handleMessageEdit(event: CustomEvent<string>) {
        const messageIndex = this.getMessageIndexFromEvent(event);
        if (messageIndex !== -1) {
            this.atMessageEdit.emit({
                messageId: this.messages[messageIndex].id,
                content: event.detail,
            });
        }
    }

    private getMessageIndexFromEvent(event: CustomEvent): number {
        const target = event.target as HTMLElement;
        const messageContainer = target.closest(
            '[data-message-index]',
        ) as HTMLElement;
        return messageContainer
            ? parseInt(messageContainer.dataset.messageIndex || '-1', 10)
            : -1;
    }

    private renderLoadingIndicator() {
        if (!this.loading) return null;

        return (
            <div
                class="flex flex-col gap-16"
                data-name="loading-container"
            >
                <at-prompt-message
                    role={'assistant'}
                    content="Typing..."
                    name={this.chatbot_title}
                    loading={true}
                    data-name="loading-message"
                />
            </div>
        );
    }

    private renderMessage(message: PromptMessage, index: number) {
        const role = message.role;
        const name =
            message.role === 'user'
                ? message.name
                : message.name || this.chatbot_title;

        const animate =
            message.role === 'assistant' ? this.response_animation : 'fade';

        return (
            <at-prompt-message
                role={role}
                content={message.content}
                name={name}
                loading={message.loading}
                error={message.error}
                error_message={message.error_message}
                score={message.score || PromptResponseScore.NONE}
                message_id={message.id}
                enable_vote={this.enable_vote}
                enable_copy={this.enable_copy}
                enable_edit={this.enable_edit}
                response_animation={animate}
                data-name={`message-${index}`}
                data-message-index={index}
            />
        );
    }

    private renderMessages() {
        return this.messages.map((message, index) =>
            this.renderMessage(message, index),
        );
    }

    render() {
        const hasMessages = this.messages && this.messages.length > 0;

        return (
            <Host
                class="block h-full"
                data-name="thread-container"
            >
                <div
                    class="flex h-full flex-col gap-16 overflow-y-auto scroll-smooth"
                    ref={(el) => (this.scrollContainer = el)}
                    data-name="scroll-container"
                >
                    {!hasMessages ? (
                        <slot name="thread-empty-state" />
                    ) : (
                        <div
                            data-name="thread-messages-container"
                            class="flex flex-col gap-16"
                        >
                            {this.renderMessages()}
                            {this.renderLoadingIndicator()}
                        </div>
                    )}
                    <slot name="thread-messages" />
                </div>
            </Host>
        );
    }
}
